.linePreloader {
    width:200px;
    height:4px;
    background:linear-gradient(to right, #f6a821, #f6a821);
    background-color: #FFFF;
    position:fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    margin:auto;
    border-radius:4px;
    background-size:20%;
    background-repeat:repeat-y;
    background-position:-25% 0;
    animation:scroll 1.2s ease-in-out infinite;
}

@keyframes scroll{
    50%{background-size:80%}
    100%{background-position:125% 0;}
}